import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function currentRatioTtm(tableName: string) {
  const title = "Current Ratio (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["currentRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Can the company pay short-term bills with current assets.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about its quick ratio or current ratio or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.currentRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.currentRatioTtm}
        />
      )
    },
  })
}

export { currentRatioTtm }
